body {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
  line-height: 56px;
  color: #333333;
  text-transform: uppercase;
}

a {
  color: #333333;
}

.container {
  box-sizing: border-box;
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  padding: 0 47px;
}

.heading__title {
  font-size: 40px;
  text-align: center;
  margin-bottom: 0;
  margin-top: 0;
}

.heading__subtitle {
  font-size: 16px;
  color: #797979;
  margin: 0 auto;
  text-align: center;
  max-width: 674px;
  padding: 0 60px;
}

@media screen and (max-width: 768px) {
  .container {
    max-width: 768px;
  }
}