.main-header {
  display: flex;
}

.logo svg {
  width: 150px;
  height: 118px;
}
.nav-bar {
  height: 100%;
}
.nav-bar__list-links {
  display: flex;
  align-items: center;
  list-style: none;
  text-transform: uppercase;
  margin: 0;
  height: 100%;

  li {
    height: 100%;
    margin-bottom: 5px;
  }

  a {
    display: flex;
    align-items: center;
    text-decoration: none;
    height: 100%;
    padding-left: 35px;
    padding-right: 45px;
  }
  a:hover {
    background: yellow;
  }
}

.search {
  display: flex;
  flex-grow: 1;
  justify-content: flex-end;
  align-items: center;
}

.search__form {
  display: flex;
  align-items: center;
  width: 100%;

  input, label {
    width: 100%;
  }

  button {
    background: transparent;
    border: none;
    padding: 0;

    svg {
      width: 33px;
      height: 33px;
    }
  }
}

